function Image(props) {
  const [urls, setUrls] = useState(null);
  useEffect(() => {
    fetchPokemon(props.src).then((url) => {
      setUrls(url);
    });
  }, []);
  return (
    <figure className="image">
      <img src={urls} alt="cute dog!" />
    </figure>
  );
}
