export async function fetchPokemon(pokemon){
  const respons =await fetch(`https://pokeapi.co/api/v2/pokemon/${pokemon}/`);
  const data=await respons.json();
  return data.sprites.front_default;
}
export async function fetchPokemonType(pokemon){
  const respons =await fetch(`https://pokeapi.co/api/v2/pokemon/${pokemon}/`);
  const data=await respons.json();
  const type=[];
  type.push(data.types[0].type.name);
  if(data.types.length===2){
    type.push(data.types[1].type.name);
  }
  return type;
}
export async function fetchPokemonStat(pokemon){
  const respons =await fetch(`https://pokeapi.co/api/v2/pokemon/${pokemon}/`);
  const data=await respons.json();
  const type=[];
  for(let i=0;i<6;i++){
    type.push(data.stats[i].base_stat);
  }
  console.log(type);
  return type;
}